<template>
	<div id="reserve-index">
		<public-header title="预定时间"></public-header>
		<div class="content">
			<div class="order-time">
				<ul>
					<li v-for="(dayItem, index) in days" :key='index' :class="{active: index == extra.dayIndex}" @click="onSelectDay(index, dayItem.day)">
						<p>{{dayItem.day_cn}}</p>
						<span>{{dayItem.day}} {{dayItem.week_cn}}</span>
					</li>
				</ul>
			</div>
			<div class="order-con">
				<div v-for="categoryItem in categorys" :key="categoryItem.id">
					<span>{{categoryItem.title}}</span>
					<p>¥{{categoryItem.reservation_price}}起订</p>
					<div class="table-list">
						<div class="table-wrap" v-for="(item, index) in reserves[categoryItem.id]" :key="index" >
							<div class="table-item" :class="{'border-gray': (extra.dayIndex == 0 && item.timeout == 0)}">
								<div v-if="(extra.dayIndex == 0 && item.timeout == 1) || extra.dayIndex > 0" @click="onSelectTime(item.time, categoryItem.id, item.total_num)">
									{{item.title}}
									<template v-if="tables_info[extra.day]">
										<template v-if="tables_info[extra.day][categoryItem.id]">
											<template v-if="tables_info[extra.day][categoryItem.id][item.time]">
												(桌位{{item.total_num - tables_info[extra.day][categoryItem.id][item.time]}})
											</template>
											<template v-else>
												(桌位{{item.total_num}})
											</template>
										</template>
										<template v-else>
											(桌位{{item.total_num}})
										</template>
									</template>
									<template v-else>
										(桌位{{item.total_num}})
									</template>
								</div>
								<div v-else @click="onSelectTime(-1, -1, 0)">{{item.time}}</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader,
		},
		data() {
			return {
				extra: {
					dayIndex: 0,
					day: '',
					time: '',
					cid: '',
				},
				categorys: {},
				reserves: {},
				days: [],
				year: '',
				tables_info: {},
				showPreLoading: true
			}
		},
		computed: {

		},
		methods: {
			...mapMutations([
				'setState'
			]),
			onLoad() {
				this.util.request({
					url: 'wmall/store/reserve/index',
					data: {
						sid: this.sid
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message)
						return false;
					}
					this.categorys = result.message.categorys;
					this.reserves = result.message.reserves;
					this.days = [...result.message.days];
					this.year = result.message.year;
					this.extra.day = this.year + '-' + this.days[0]['day'];
					this.tables_info = result.message.tables_info;
				})
			},
			onSelectDay(index, value) {
				this.extra.dayIndex = index;
				this.extra.day = this.year + '-' + value;
				this.setState({type:'reserveExtra', key:'day', val:this.extra.day});
			},
			onSelectTime(time, cid, total_num) {
				if(time == -1) {
					this.$toast('该时间不能预定点餐');
					return false;
				}
				if(this.tables_info[this.extra.day] && this.tables_info[this.extra.day][cid]) {
					if(total_num <= this.tables_info[this.extra.day][cid][time]) {
						this.$toast('该桌台类型下没有可以预订的桌位');
						return false;
					}
				}
				if(this.extra.dayIndex == 0) {
					this.extra.day = this.year + '-' + this.days[0].day;
					this.setState({type:'reserveExtra', key:'day',val:this.extra.day});
				}
				this.extra.time = time;
				this.extra.cid = cid;
				this.setState({type:'reserveExtra', key:'cid',val:cid});
				this.setState({type:'reserveExtra', key:'time',val:time});
				this.$router.push(this.util.getUrl({path: '/tangshi/pages/reserve/create?sid=' + this.sid}));
			},
		},
		mounted() {
			this.onLoad()
		},
		created() {
			this.query = this.$route.query;
			if(this.query) {
				this.sid = this.query.sid;
			}
		}
	}
</script>

<style>
	#reserve-index .van-nav-bar{
		background-color: #ff2d4b!important;
		color: #fff!important;
	}
	#reserve-index .van-hairline--bottom .van-icon-left{
		color: #fff!important;
	}
	#reserve-index .content .order-time ul{
		height: 44px;
		display: table;
		width: 100%;
		background: #eee;
	}
	#reserve-index .content .order-time ul li.active{
		color: #fff;
		background: #ff2d4b;
	}
	#reserve-index .content .order-time ul li{
		height: 44px;
		text-align: center;
		padding: 2px;
		display: table-cell;
	}
	#reserve-index .content .order-time ul li p{
		margin: 0;
		font-size: 14px;
		line-height: 1.4;
	}
	#reserve-index .content .order-time ul li span{
		font-size: 12px;
		display: block;
		line-height: 1.4;
	}
	#reserve-index .content .order-con{
		background: #fff;
		padding: 15px 10px 1px;
	}
	#reserve-index .content .order-con span{
		display: block;
		font-size: 17px;
		font-weight: bold;
		line-height: 1.2;
		border-left: 3px solid #ff2d4b;
		padding-left: 6px;
	}
	#reserve-index .content .order-con p{
		font-size: 12px;
		color: #999;
		margin: 10px 0;
		line-height: 18px;
	}
	#reserve-index .content .order-con .table-list{
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		border-bottom: 10px solid #eeeeee;
		margin-bottom: 18px;
	}
	#reserve-index .content .order-con .table-list .table-wrap{
		width: 33.33%;
		text-align: center;
	}
	#reserve-index .content .order-con .table-list .table-wrap .table-item.border-gray{
		border: 1px solid #959595;
	}
	#reserve-index .content .order-con .table-list .table-wrap .table-item.border-gray div{
		color: #959595;
	}
	#reserve-index .content .order-con .table-list .table-wrap .table-item{
		width: 100px;
		height: 28px;
		line-height: 28px;
		border:  1px solid #ff2d4b;
		border-radius: 4px;
		text-align: center;
		font-size: 14px;
		margin: 0 auto 10px;
	}
	#reserve-index .content .order-con .table-list .table-wrap .table-item div{
		color: #ff2d4b;
	}
</style>


